import React, { Component } from 'react'
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardActions, CardMenu, Button, IconButton, CardText } from 'react-mdl'

class project extends Component {
    constructor(props) {
        super(props)
        this.state = {
            activeTab: 0
        };
    }

    toggleCategories() {
        if (this.state.activeTab === 0) {
            return (
                <div className='project-grid' col={12}>
                    <Card shadow={5} style={{ minWidth: '450' , margin: 'auto' }}>
                        <CardTitle style={{ color: '#fff', height: '170px', background: 'black'}}>
                            Air-bill Website
                   </CardTitle>
                        <CardText style={{minHeight :'140px'}}>
                            Air-bill is an aircraft booking and scheduling website. The main
                            purpose of the site is to show the available and booked slots on
                            a fully interactive calendar, which also allows
                            the users to book flights, on the empty slots. Notify users with an
                        automatically generated mail in case of flight delay, or maintainence issues.</CardText>
                        <CardActions border>
                            <Button colored>Github</Button>
                            {/* <Button colored>CodePen</Button>
                            <Button colored>Live maybe</Button> */}

                        </CardActions>
                        <CardMenu style={{ color: '#fff' }}>
                            <IconButton name="share" />
                        </CardMenu>
                    </Card>

                    <Card shadow={5} style={{ minWidth: '450', margin: 'auto' }}>
                        <CardTitle style={{ color: '#fff', height: '170px', background: 'black'}}>
                            Trip Advisor Website
                   </CardTitle>
                        <CardText style={{minHeight :'140px'}}>
                            Trip advisor site is an online trip organizing website, which allows
                            the users to book transportation, accommodation and a trip guide, for the trip.
                            User can plan a whole trip, estimate the cost of traveling and accommodation.
                            And also shows user’s location using the embedded map on the website which shows
                         the route between user’s location and destination point.</CardText>
                        <CardActions border>
                            <Button colored>Github</Button>
                            {/* <Button colored>CodePen</Button>
                            <Button colored>Live maybe</Button> */}

                        </CardActions>
                        <CardMenu style={{ color: '#fff' }}>
                            <IconButton name="share" />
                        </CardMenu>
                    </Card>


                    <Card shadow={5} style={{ minWidth: '450', margin: 'auto' }}>
                        <CardTitle style={{ color: '#fff', height: '170px', background: 'black'}}>
                            React Project 1
                   </CardTitle>
                        <CardText style={{minHeight :'140px'}}>
                            Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                    Lorem Ipsum has been the industry's standard dummy text.</CardText>
                        <CardActions border>
                            <Button colored>Github</Button>
                            {/* <Button colored>CodePen</Button>
                            <Button colored>Live maybe</Button> */}

                        </CardActions>
                        <CardMenu style={{ color: '#fff' }}>
                            <IconButton name="share" />
                        </CardMenu>
                    </Card>



                </div>

            )
        }
        else if (this.state.activeTab === 1) {
            return (<div className='project-grid' col={12}>
                <Card shadow={5} style={{ minWidth: '450', margin: 'auto' }}>
                    <CardTitle style={{ color: '#fff', height: '170px', background: 'black'}}>
                        Digital Map Application
           </CardTitle>
                    <CardText style={{minHeight :'140px'}}>
                        DMP, is a digital map application for air crafts, which displays tactical raster maps, vector maps, geo-strategical vector layers and geographical
                        information on MFD
                 (Multi-functional Display), that assist the pilot during flight, in real time. The application is developed using NVIDIA jetson TK1 development kit.</CardText>
                    <CardActions border>
                        {/* <Button colored>Github</Button> */}
                        {/* <Button colored>CodePen</Button>
                    <Button colored>Live maybe</Button> */}

                    </CardActions>
                    <CardMenu style={{ color: '#fff' }}>
                        <IconButton name="share" />
                    </CardMenu>
                </Card>

                <Card shadow={5} style={{ minWidth: '450', margin: 'auto' }}>
                    <CardTitle style={{ color: '#fff', height: '170px', background: 'black'}}>
                        GSE
           </CardTitle>
                    <CardText style={{minHeight :'140px'}}>
                        GSE (Ground Support Equipment) is custom build GIS system build specifically for the digital map application.
                 Basic purpose of the GSE is to prepare a custom map for the flight mission using geospatial data and configuring the digital map application.</CardText>
                    <CardActions border>
                        {/* <Button colored>Github</Button> */}
                        {/* <Button colored>CodePen</Button> */}
                        {/* <Button colored>Live maybe</Button> */}

                    </CardActions>
                    <CardMenu style={{ color: '#fff' }}>
                        <IconButton name="share" />
                    </CardMenu>
                </Card>


                <Card shadow={5} style={{ minWidth: '450', margin: 'auto' }}>
                    <CardTitle style={{ color: '#fff', height: '170px', background: 'black'}}>
                        CDU
           </CardTitle>
                    <CardText style={{minHeight :'140px'}}>
                        CDU (Control Display Unit)
                        is a configuration unit present in aircrafts which keeps the count of weapons, missiles,
                fuel and also perform system testing of the aircraft's various modules.  </CardText>
                    <CardActions border>
                        {/* <Button colored>Github</Button> */}
                        {/* <Button colored>CodePen</Button>
                    <Button colored>Live maybe</Button> */}

                    </CardActions>
                    <CardMenu style={{ color: '#fff' }}>
                        <IconButton name="share" />
                    </CardMenu>
                </Card>


                <Card shadow={5} style={{ minWidth: '450',  margin: 'auto' }}>
                    <CardTitle style={{ color: '#fff', height: '170px', background: 'black'}}>
                    Height Clearance Application
                    </CardTitle>
                    <CardText style={{minHeight :'140px'}}>
                    Height Clearance Application is critical warning generation application for air craft. It generates warnings for the pilot on the basis of altitude of air craft. Basically it scans 
                    the height of area ahead of the aircraft from DEM ( Digital Elevation Models) and generate critical warnings on the basis of current altitude.  </CardText>
                    <CardActions border>
                        {/* <Button colored>Github</Button> */}
                        {/* <Button colored>CodePen</Button>
                    <Button colored>Live maybe</Button> */}

                    </CardActions>
                    <CardMenu style={{ color: '#fff' }}>
                        <IconButton name="share" />
                    </CardMenu>
                </Card>


            </div>
            )
        }
        else if (this.state.activeTab === 2) {
            return (<div>
                <h1>
                    JavaScript
                </h1>
            </div>)
        }
    }

    render() {
        return (
            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
                    <Tab> Web Development </Tab>
                    <Tab> Desktop Applications </Tab>
                    {/* <Tab> JavaScript </Tab> */}
                </Tabs>
                {/* <section className="projects_grid"> */}
                <Grid>
                    <Cell col={12}>
                    <div className="content"> {this.toggleCategories()}</div>
                    </Cell>
                </Grid>

                {/* </section> */}
            </div>
        )
    }
}

export default project
