import React, { Component } from 'react'
import { Grid, Cell } from 'react-mdl'
import profileImg from '../assests/img.jpg'

class landing_page extends Component {
    render() {
        return (
            <div style={{ width: '100%', margin: 'auto' }}>
                <Grid className="landing-grid">
                    <Cell col={12}>
                        <img src={profileImg} className="avatar-img"  style={{ height: '250px' , borderRadius: '150px'}}/>
                        <div className="banner-text">
                            <h1> Hamza Mehmood </h1>
                            <hr />
                            <p>  Software Developer </p>
                           
                            <div className="social-links">
                                
                                <a href="https://www.linkedin.com/in/hamzameh" target="_blank"  rel="noopener no referrer">
                                    <i className="fa fa-linkedin-square" aria-hidden="true"></i>
                                </a>

                                <a href="https://github.com/dork7" target="_blank"  rel="noopener no referrer">
                                    <i className="fa fa-github-square" aria-hidden="true"></i>
                                </a>

                                {/* <a href="http://google.com" target="_blank"  rel="noopener no referrer">
                                    <i className="fa fa-free-code-camp" aria-hidden="true"></i>
                                </a> */}

                                {/* <a href="http://google.com" target="_blank"  rel="noopener no referrer">
                                    <i className="fa fa-youtube-square" aria-hidden="true"></i>
                                </a> */}

                            </div>
                        </div>
                    </Cell>
                </Grid>

            </div>
        )
    }
}

export default landing_page
