import React, { Component } from 'react'
import { Tabs, Tab, Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl'
import profileImg from '../assests/img.jpg'

class contact extends Component {
    render() {
        return (

            <div className="contact-body">
                <Grid className="contact-grid">
                    <Cell col={6}>
                        <h2> Hamza Mehmood </h2>
                        <img src={profileImg}
                            alt="avatar"
                            style={{ height: '250px' , borderRadius : '300px' }} />
                        <p style={{ width: '75%', margin: 'auto', paddingTop: '1em' }}>
                 
                        </p>
                    </Cell>
                    <Cell col={6}>
                        <h2>Contact Me</h2>
                        <hr />
                        <div className="contact-list">
                            <List>
                                <ListItem>
                                    <ListItemContent style={{
                                        fontSize: '30px',
                                        fontFamily: 'Anton'
                                    }}>
                                        <i className="fa fa-phone-square" />92-345-5293564</ListItemContent>
                                </ListItem>
                                <ListItem>
                                    <ListItemContent style={{
                                        fontSize: '30px',
                                        fontFamily: 'Anton'
                                    }}>
                                        <i className="fa fa-envelope" />hamzameh122@gmail.com</ListItemContent>
                                </ListItem>
                                <ListItem>
                                    <ListItemContent style={{
                                        fontSize: '30px',
                                        fontFamily: 'Anton'
                                    }}>
                                        <i className="fa fa-linkedin" />@hamzameh</ListItemContent>
                                </ListItem>

                                <ListItem>
                                    <ListItemContent style={{
                                        fontSize: '30px',
                                        fontFamily: 'Anton'
                                    }}>
                                        <i className="fa fa-github" />github.com/dork7</ListItemContent>
                                </ListItem>



                            </List>
                        </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default contact
